import React from 'react';
import "./style.css";
import Jumbotron from "../Jumbotron";
import API from "../../utils/API";
import {Col, Container, Row} from "../Grid";
import SearchForm from "../SearchForm/SearchForm";

export default class Main extends React.Component {
    state = {
        recipes: [],
        beer: [],
        search: "",
        ingredients: "",
        title: "",
        food_pairing: "",
    };

    
    // componentDidMount(){
       
    //     document.body.className="body-component-a" // Or set the class
    // }


    recipeSave = (id) => {
        const recipe = this.state.recipes.find(recipe => recipe.recipe_id === id);
        // API.getFood(recipe).then(res => this.getFood());
        console.log(id);
    }
    
    getFood = (keyword) => {
        this.setState({ search: keyword})
        // console.log(this.state.search);
        API.getFood(this.state.search)
        .then(res => {
            
            console.log(res.data);
            // const recipes = res.data.recipes;
            
            this.setState({ recipes: res.data.recipes });
        })};

        
        getBeer = (keyword) => {
            this.setState({ search: keyword})
            // console.log(this.state.search);
            API.getBeer(this.state.search)

            .then(res => {
                
                console.log(res.data[0].food_pairing);
                // const recipes = res.data.recipes;
                // console.log(res.data + "hello");
                // this.setState({ food_pairing: res.data.food_pairing });
            })};
        handleInputChange = event => {
            this.setState({ search: event.target.value });
          };
          handleFormSubmit = event => {
            event.preventDefault();
            
            this.getFood();
            this.getBeer();
          };

          render(){
    
            return (
               
                <Container>
                   <Jumbotron />
                    <div className="main">
            <Row>
                <Col size="col-4">

                <div className="results">
<SearchForm 
    handleFormSubmit={this.handleFormSubmit}
    handleInputChange={this.handleInputChange}/>
    
<div>
    { this.state.recipes.map(recipes => <li onClick={this.recipeSave} key={recipes.recipe_id} id={recipes.recipe_id}>  {recipes.title}</li>)}
</div>

</div> 
</Col>
</Row>
</div>
</Container>

);
};
};